body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #121212;
  color: #E6E6FA;
  scroll-behavior: smooth;
}

nav {
  background-color: #1E1E2F;
  color: #E6E6FA;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: 0 2px 8px rgba(255, 111, 145, 0.3); /* pink glow */
}

nav a {
  margin: 0 1rem;
  color: #E6E6FA;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

nav a:hover {
  color: #FF3D6B;
}

header.hero {
  background: linear-gradient(135deg, #2B2B44 0%, #121212 100%);
  color: #FF6F91;
  padding: 6rem 2rem;
  position: relative;
}

header.hero .overlay h1 {
  font-size: 3rem;
  margin-bottom: 0.5rem;
  text-shadow: 0 0 12px #FF6F91;
}

header.hero .overlay p {
  font-size: 1.3rem;
  color: #E6E6FA;
  margin-bottom: 2rem;
}

.cta-button {
  padding: 0.8rem 2rem;
  font-size: 1.1rem;
  background-color: #FF6F91;
  border: none;
  color: #121212;
  cursor: pointer;
  border-radius: 30px;
  text-decoration: none;
  box-shadow: 0 0 10px #FF6F91;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

.cta-button:hover {
  background-color: #FF3D6B;
  box-shadow: 0 0 15px #FF3D6B;
  color: #fff;
}

section {
  padding: 4rem 2rem;
  text-align: center;
  background-color: #1E1E2F;
  margin: 1rem 0;
  border-radius: 12px;
  box-shadow: 0 0 10px rgba(255, 111, 145, 0.2);
}

.project-card {
  background-color: #2B2B44;
  border-radius: 10px;
  padding: 2rem;
  margin: 2rem auto;
  max-width: 700px;
  box-shadow: 0 0 15px #FF6F91;
  color: #E6E6FA;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card:hover {
  transform: scale(1.05);
  box-shadow: 0 0 25px #FF3D6B;
}

footer {
  background-color: #1E1E2F;
  color: #E6E6FA;
  text-align: center;
  padding: 1rem;
  margin-top: 3rem;
  box-shadow: inset 0 1px 0 #FF6F91;
}

a {
  color: #FF6F91;
  font-weight: 600;
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: #FF3D6B;
}
